import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../config/constants/content'
import { VAK_PATTERNS } from '../../config/constants/reference'
import { ERROR_TYPE, REF_LIST_KEYS } from '../../config/enums'
import { CheckConfig, FrameHeightsData, PdfDataType } from '../../types'
import { ContentListConfigType } from '../checkPagesFidelity'
import { checkListLength } from './src/checkListLength'
import { checkRefListOrder } from './src/checkRefListOrder'
import { checkReferenceElRes } from './src/checkReferenceElRes'
import { checkReferenceNumberFormat } from './src/checkReferenceNumberFormat'
import { getIsElRes } from './src/getIsElRes'
import { getReferenceList } from './src/getReferenceList'

export const checkReferenceList = async (
  pdfData: PdfDataType,
  setErrors: SetErrors,
  {
    contentListConfig,
    refListMinLen,
    frameHeightsData,
  }: {
    contentListConfig: ContentListConfigType
    refListMinLen?: CheckConfig['refListMinLen']
    frameHeightsData: FrameHeightsData
  }
): Promise<{ length: number }> => {
  const referenceList = await getReferenceList(pdfData, contentListConfig, CONTENT_LIST, frameHeightsData)

  if (!referenceList.length) {
    setErrors(ERROR_TYPE.referenceList, REF_LIST_KEYS.rule0, CONTENT_LIST.litList.toUpperCase())
    return { length: 0 }
  }

  if (typeof refListMinLen === 'number') {
    checkListLength(referenceList, refListMinLen, setErrors)
  }
  checkRefListOrder(referenceList, setErrors)

  for (const item of referenceList) {
    checkReferenceNumberFormat(setErrors, item)

    if (getIsElRes(item)) {
      checkReferenceElRes(setErrors, item, VAK_PATTERNS)
    }
  }

  return { length: referenceList.length }
}
