export const defineAbbr = (list: string[]): string[] => {
  const arr: string[] = []
  const pattern = /[А-ЯҐЄІЇA-Z\d]+(?=\s)/
  let prevValue = ''

  list.forEach((item) => {
    const match = pattern.exec(item)
    if (match) {
      if (prevValue) {
        arr.push(prevValue)
      }
      prevValue = item
    } else {
      prevValue = prevValue + ' ' + item
    }
  })

  if (prevValue) {
    arr.push(prevValue)
  }
  return arr
}
