/**
 * helper to concat title from several lines
 * @param startStr 
 * @param endStr 
 * @returns 
 */
export const concatTitle = (startStr: string, endStr: string): string => {
  const str = (startStr + endStr)
    .replace(/\.{2,}/g, '')
    .replace('. .', '')
    .replace(/\d+$/, '')
    .replace(/\.$/, '')
    .trim()

  return str
}