import { ContentListConfigType } from '..'
import { SetErrors } from '../../..'
import { ERROR_TYPE, PICTURES_TABLES_ERROR_KEYS } from '../../config/enums'
import { FrameHeightsData, PdfDataType } from '../../types'
import { getPagesStringRows } from '../../utils/pdfFormaters/getPagesStringRows'
import { checkPictures } from './src/checkPictures'
import { checkTables } from './src/checkTables'
import { getChaptersPages } from './src/getChaptersPages'
import { getComplexName } from './src/getComplexName'

export const checkTablesAndPictures = async (
  pdfData: PdfDataType,
  setErrors: SetErrors,
  {
    contentListConfig,
    frameHeightsData,
  }: {
    contentListConfig: ContentListConfigType
    frameHeightsData: FrameHeightsData
  }
): Promise<void> => {
  if (!contentListConfig.length) {
    setErrors(ERROR_TYPE.picturesAndTables, PICTURES_TABLES_ERROR_KEYS.rule0)
    return
  }

  const chaptersData: [number, number][] = await getChaptersPages(contentListConfig, pdfData.numPages)

  for (let i = 0; i < chaptersData.length; i++) {
    const [start, end] = chaptersData[i]
    const chapterNum = i + 1

    const rows = await getPagesStringRows(pdfData, start, end, undefined, frameHeightsData)
    const tablArr: string[] = []
    const pictArr: string[] = []

    for (let i = 0; i < rows.length; i++) {
      const row = rows[i]
      const isTable = row.match(/табл/gi)
      const isPicture = row.match(/рис\./gi) // todo: remove \. and test

      const name = rows[i + 1] ? getComplexName(row, rows[i + 1]) : row

      if (isTable) {
        tablArr.push(name)
      }
      if (isPicture) {
        pictArr.push(name)
      }
    }

    checkTables(chapterNum, tablArr, setErrors)
    checkPictures(chapterNum, pictArr, setErrors)
  }
}
