import { ContentListConfigType } from '../..'
import { FrameHeightsData, PdfDataType } from '../../../types'
import { getPageStringRows } from '../../../utils/pdfFormaters/getPageStringRows'

export const getAddArr = async (
  pdfData: PdfDataType,
  list: ContentListConfigType,
  title: string,
  additionItemTitle: string,
  frameHeightsData: FrameHeightsData
): Promise<string[] | null> => {
  const addArr: string[] = []
  const addConfig = list.find((item) => item.title.toLowerCase() === title.toLowerCase())
  if (!addConfig) return null
  const numPages = pdfData.numPages

  for (let i = addConfig.page + 1; i <= numPages; i++) {
    const lines = await getPageStringRows(pdfData, i, frameHeightsData)
    for (const line of lines) {
      if (line.toLowerCase().includes(additionItemTitle.toLowerCase())) {
        addArr.push(line.trim())
      }
    }
  }

  return addArr
}
