import { CONTENT_LIST } from '../../../config/constants/content'
import { FrameHeightsData, PdfDataType } from '../../../types'
import { getContentListPageNumber } from '../../../utils/getContentListPageNumber'
import { getPagesStringRows } from '../../../utils/pdfFormaters/getPagesStringRows'
import { ContentListConfigType } from '../../checkPagesFidelity'
import { getListFromStringRows } from './getListFromStringRows'

export const getReferenceList = async (
  pdfData: PdfDataType,
  contentListConfig: ContentListConfigType,
  constants: typeof CONTENT_LIST,
  frameHeightsData: FrameHeightsData
): Promise<string[]> => {
  const pageRefListNumber = getContentListPageNumber(contentListConfig, constants.litList)
  const pageAdditionNumber = getContentListPageNumber(contentListConfig, constants.addition)

  if (typeof pageRefListNumber === 'number') {
    let end = 0
    if (pageAdditionNumber) {
      end = pageAdditionNumber - 1
    } else {
      end = pdfData.numPages
    }

    const rows = await getPagesStringRows(pdfData, pageRefListNumber, end, constants.litList, frameHeightsData)

    return getListFromStringRows(rows)
  }
  return []
}
