import { FrameHeightsData, PdfDataType } from '../../../types'
import { getPagesStringRows } from '../../../utils/pdfFormaters/getPagesStringRows'
import { defineAbbr } from './defineAbbr'

/**
 *
 * @param pdfData
 * @param setErrors
 * @returns
 */
export const getAbbreviationList = async (
  pdfData: PdfDataType,
  abbreviationListConfig: { start: number; length: number },
  abbrTitle: string,
  frameHeightsData: FrameHeightsData
): Promise<string[]> => {
  const { start, length } = abbreviationListConfig

  const rows = await getPagesStringRows(pdfData, start, start + length - 1, abbrTitle, frameHeightsData)

  return defineAbbr(rows)
}
