import { SetErrors } from '../../../..'
import { ADD_ERROR_KEYS, ERROR_TYPE } from '../../../config/enums'
import { isSorted } from '../../../utils/isSorted'
import { checkAddItem } from './checkAddItem'

export const checkAddItems = (addArr: string[], additionItemTitle: string, setErrors: SetErrors): void => {
  const lettersArr = addArr.map((item) => {
    checkAddItem(item, additionItemTitle, setErrors)
    const arr = item.split(' ')
    if (arr.length < 2 || arr.length > 2) {
      return item
    }
    return arr[1]
  })

  const { error } = isSorted(lettersArr)
  if (error) {
    setErrors(ERROR_TYPE.addition, ADD_ERROR_KEYS.rule1, lettersArr.join(', '))
  }
}
