import { getIsCapitalize } from './getIsCapitalize'

/**
 *
 * @param item - item from list
 * @param chapterNumber - chapter number
 * @param subItemNumber - sub item number
 * @returns
 */
export const checkChapterItem = (
  item: string,
  chapterNumber: number,
  subItemNumber: number
): {
  isNumberCorrect: boolean
  isTileCapitalize: boolean
} => {
  const [number, ...titleArr] = item.split(' ')
  const isNumberCorrect = number === `${chapterNumber}.${subItemNumber}`
  const isTileCapitalize = getIsCapitalize(titleArr[0])

  return { isNumberCorrect, isTileCapitalize }
}
