import { SetErrors } from '../../../..'
import { CONTENT_LIST } from '../../../config/constants/content'
import { ERROR_TYPE, PAGES_FIDELITY_ERROR_KEYS } from '../../../config/enums'
import { findChapterByName } from './findChapterByName'
import { findIndexByMatch } from './findIndexByMatch'
import { ChapterTitleType } from './getChaptersData'
import { getIsUpperCase } from './getIsUpperCase'

export const checkAdditionalItems = (
  setErrors: SetErrors,
  list: string[],
  chapters: ChapterTitleType[],
  constants: typeof CONTENT_LIST
): number => {
  let chaptersEndPosition
  const abstractIndex = findIndexByMatch(list, constants.abstract)
  const abstractEnIndex = findIndexByMatch(list, constants.abstractEng)
  const introductionIndex = findIndexByMatch(list, constants.introduction)
  const litListIndex = findIndexByMatch(list, constants.litList)
  const shortNamesIndex = findIndexByMatch(list, constants.abbreviations)
  const additionIndex = findIndexByMatch(list, constants.addition)
  const isAbstract = abstractIndex !== -1
  const isAbstractEn = abstractEnIndex !== -1
  const isIntroduction = introductionIndex !== -1
  const isLitList = litListIndex !== -1
  const isShortNames = shortNamesIndex !== -1
  const isAddition = additionIndex !== -1

  const checkTitleRegister = (title: string) => {
    if (!getIsUpperCase(title)) {
      setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule2, title)
    }
  }

  if (!isAbstract) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10, constants.abstract.toUpperCase())
  } else {
    checkTitleRegister(list[abstractIndex])
    if (abstractIndex !== 0) {
      setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10_1, constants.abstract)
    }
  }

  if (!isAbstractEn) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10, constants.abstractEng.toUpperCase())
  } else {
    checkTitleRegister(list[abstractEnIndex])
    if (isAbstract && abstractIndex > abstractEnIndex) {
      setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10_1, constants.abstractEng)
    }
  }

  if (!isIntroduction) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10, constants.introduction.toUpperCase())
  } else if (
    (isAbstract && abstractIndex > introductionIndex) ||
    (isAbstractEn && abstractEnIndex > introductionIndex)
  ) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10_1, constants.introduction)
  }

  const lastChapterConclusionIndex = chapters[chapters.length - 1].conclusionPosition
  if (lastChapterConclusionIndex) {
    chaptersEndPosition = lastChapterConclusionIndex
  }

  const mainConclusion = findChapterByName(list, constants.conclusion)
  if (!mainConclusion) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10, constants.conclusion.toUpperCase())
  } else {
    checkTitleRegister(mainConclusion)
  }

  if (!isLitList) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule10, constants.litList.toUpperCase())
  } else {
    checkTitleRegister(list[litListIndex])
  }

  if (isShortNames) {
    checkTitleRegister(list[shortNamesIndex])
  }

  if (isAddition) {
    checkTitleRegister(list[additionIndex])
  }

  if (!chaptersEndPosition) {
    chaptersEndPosition = isLitList ? litListIndex - 1 : isAddition ? additionIndex - 1 : list.length - 1
  }

  return chaptersEndPosition
}
