import { ERROR_TYPE } from './src/config/enums'
import { ERRORS } from './src/config/errors'
import {
  checkAbbreviationList,
  checkAddService,
  checkFrameService,
  checkPagesFidelity,
  checkReferenceList,
  checkReferenceOrder,
  checkTablesAndPictures,
} from './src/services'
import { CheckConfig, ErrorsType, FrameHeightsData } from './src/types'
import { parsePdfByData } from './src/utils/pdfFormaters/parsePdfByData'

export type SetErrors = <T extends ERROR_TYPE>(
  type: T,
  rule: keyof (typeof ERRORS)[T],
  str1?: string,
  str2?: string
) => void

export async function check(data: Uint8Array | ArrayBuffer | Buffer, config?: CheckConfig) {
  const results: ErrorsType = {}
  const { refListMinLen, isChapterConclusionRequired, chapterSize, frameConfig } = config || {}

  const setErrors: SetErrors = (type, rule, str1 = '', str2 = '') => {
    let str = ERRORS[type][rule] as string
    if (!results[type]) {
      results[type] = { [rule]: [] }
    }
    // @ts-ignore
    if (results[type] && !results[type][rule]) {
      // @ts-ignore
      results[type][rule] = []
    }
    if (str1) {
      str = str.replace('{str1}', str1)
    }
    if (str2) {
      str = str.replace('{str2}', str2)
    }

    // @ts-ignore
    ;(results[type][rule] as string[]).push(str)
  }

  try {
    const pdfData = await parsePdfByData(data)
    let frameHeightsData: FrameHeightsData = undefined
    if (frameConfig) {
      frameHeightsData = await checkFrameService(pdfData, setErrors, frameConfig)
    }

    const contentListConfig = await checkPagesFidelity(
      pdfData,
      setErrors,
      {
        isChapterConclusionRequired,
        chapterSize,
      },
      frameHeightsData
    )
    const { length } = await checkReferenceList(pdfData, setErrors, {
      contentListConfig,
      refListMinLen,
      frameHeightsData,
    })
    await checkReferenceOrder(pdfData, setErrors, { contentListConfig, length, frameHeightsData })
    await checkAbbreviationList(pdfData, setErrors, { contentListConfig, frameHeightsData })
    await checkAddService(pdfData, setErrors, { contentListConfig, frameHeightsData })
    await checkTablesAndPictures(pdfData, setErrors, { contentListConfig, frameHeightsData })
  } catch (e) {
    console.error(e)
  }

  return results
}
