/**
Need to define link for table or picture with two lines
 */
export const getComplexName = (str1: string, str2: string): string => {
  let name = str1
  // if str1 ends with (табл. or (рис. need to add check next line and add substring to name
  // substring should be from str2 and contains number.number and be on the start line position
  if (str1.endsWith('(табл.') || str1.endsWith('(рис.') || str1.endsWith('(табл. ') || str1.endsWith('(рис. ')) {
    // const regExp = /\d+\.\d+/
    // const match = str2.match(regExp)
    // if (match) {
    //   name = `${str1} ${match[0]})`
    // }
    name = `${str1} ${str2}`
  }

  return name
}
