import { FrameHeightsData, PdfDataType } from '../../types'
import { getPageStringRows } from './getPageStringRows'

export async function getPagesStringRows(
  pdfData: PdfDataType,
  start: number,
  end: number,
  title?: string,
  frameHeightsData?: FrameHeightsData
): Promise<string[]> {
  const rows: string[][] = []
  for (let i = start; i <= end; i++) {
    const res = await getPageStringRows(pdfData, i, frameHeightsData)
    if (title && i === start && res[0].toLowerCase() === title.toLowerCase()) {
      res.shift() // remove title
    }
    rows.push(res)
  }

  const result = rows.flat()

  return result
}
