/**
 *
 * @param text - text of chapter (ex. Розділ 1. Назва розділу)
 * @param chapterOriginalName - original name of chapter (ex. Розділ)
 * @returns chapter number (ex. 1)
 */
// export const getChapterNumber = (text: string, chapterOriginalName: string): string => {
//   return text.slice(chapterOriginalName.length).trim().split(' ')[0]
// }

export function getChapterNumber(chapterName: string, originalChapterName: string): string | undefined {
  const regex = new RegExp(`${originalChapterName} (\\d+)`)
  const match = chapterName.match(regex)

  if (match && match[1]) {
    return match[1]
  } else {
    return
  }
}
