import { SetErrors } from '../../../..'
import { CONTENT_LIST } from '../../../config/constants/content'
import { ERROR_TYPE, PAGES_FIDELITY_ERROR_KEYS } from '../../../config/enums'
import { ContentListCheckConfig } from '../../../types'
import { checkAdditionalItems } from './checkAdditionalItems'
import { checkChapterItem } from './checkChapterItem'
import { getChapterNumber } from './getChapterNumber'
import { getChaptersData } from './getChaptersData'
import { getIsCapitalize } from './getIsCapitalize'
import { getIsDotInEnd } from './getIsDotInEnd'
import { getIsUpperCase } from './getIsUpperCase'

export const checkList = (
  setErrors: SetErrors,
  list: string[],
  constants: typeof CONTENT_LIST,
  isChapterConclusionRequired: ContentListCheckConfig['isChapterConclusionRequired'],
  chapterSize: ContentListCheckConfig['chapterSize']
) => {
  const chapters = getChaptersData(list, constants.chapter, constants.conclusion)

  const chaptersEndPosition = checkAdditionalItems(setErrors, list, chapters, constants)

  chapters.forEach(({ chapter, conclusion, chapterPosition, conclusionPosition }, chapterIndex) => {
    const chapterNumber = getChapterNumber(chapter, constants.chapter)

    if (!getIsUpperCase(chapter)) {
      setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule2, chapter)
    }

    if (chapterNumber && chapterNumber !== (chapterIndex + 1).toString()) {
      setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule3, chapter)
    }

    if (isChapterConclusionRequired) {
      if (conclusion && !getIsCapitalize(conclusion)) {
        setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule4, conclusion)
      }

      if (!conclusion) {
        setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule8, (chapterIndex + 1).toString())
      }
    }

    if (chapterNumber && !getIsDotInEnd(chapterNumber)) {
      setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule7, chapter)
    }

    const items = list.slice(chapterPosition + 1, conclusionPosition ?? chaptersEndPosition)

    if (chapterSize) {
      if (items.length < chapterSize[0] || items.length > chapterSize[1]) {
        setErrors(
          ERROR_TYPE.pagesFidelity,
          PAGES_FIDELITY_ERROR_KEYS.rule5,
          (chapterIndex + 1).toString(),
          `${chapterSize[0]}-${chapterSize[1]}`
        )
      }
    }

    items.forEach((item, index) => {
      const { isNumberCorrect, isTileCapitalize } = checkChapterItem(item, chapterIndex + 1, index + 1)
      if (!isNumberCorrect) {
        setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule6, item)
      }
      if (!isTileCapitalize) {
        setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule4, item)
      }
    })
  })
}
