// todo: simple check for 1 uppercase and other lowercase

/**
 *
 * @param string - string to check
 * @returns - true if capitalize
 */
export const getIsCapitalize = (string: string): boolean => {
  if (!string) {
    return false
  }
  const start = string[0]
  const end = string.slice(1)
  const isCapitalize = start === start.toUpperCase() && end === end.toLowerCase()
  return isCapitalize
}
