import { SetErrors } from '../../../..'
import { ERROR_TYPE, PICTURES_TABLES_ERROR_KEYS } from '../../../config/enums'

export const checkPictureLinkFormat = (link: string, setErrors: SetErrors) => {
  const pattern = /\(див\.\sрис\.\s\d+\.\d+\)|\(рис\.\s\d+\.\d+\)/
  const match = link.match(pattern)
  if (!match) {
    setErrors(ERROR_TYPE.picturesAndTables, PICTURES_TABLES_ERROR_KEYS.rule4, link)
  }
}
