export function findReferenceMatches(inputString: string): string[] {
  const pattern = /(.{2})\[([\d\s,,-]+)\](.)/g
  const matches: string[] = []
  let result

  while ((result = pattern.exec(inputString)) !== null) {
    matches.push(`${result[1]}[${result[2]}]${result[3]}`)
  }
  return matches
}
