import { SetErrors } from '../../../..'
import { checkIsUsed } from './checkIsUsed'
import { checkOrder } from './checkOrder'
import { getTransformedReference } from './getTransformedReference'
import { trimAndReplaceDashes } from './trimAndReplaceDashes'

/**
 errors:
 rule5: 'Літературні джерела мають бути розташовані в порядку зростання. Розпізнані номери: {str1}',
 rule6: 'Літературні джерела які немають посилань: {str1}' 
 * @param arr 
 * @param setErrors 
 */
export const checkReferenceFidelity = (arr: string[], setErrors: SetErrors, limit: number): void => {
  const list = arr.map((item) => item.slice(3, item.length - 2)).join(',')
  const references = trimAndReplaceDashes(list)

  // todo: check perfomance
  const numbers: number[] = getTransformedReference(references)

  checkOrder(list, numbers, setErrors)
  checkIsUsed(limit, numbers, setErrors)
}
