import { SetErrors } from '../../../..'
import { ERROR_TYPE, PICTURES_TABLES_ERROR_KEYS } from '../../../config/enums'

export const checkPictureTitleFormat = (chapterNum: number, index: number, content: string, setErrors: SetErrors) => {
  const pattern = /^\.\s\d+\.\d+\s[A-ZА-ЯЁЄІЇ].+/
  const correct = content.startsWith(`. ${chapterNum}.${index} `)

  if (!correct || !pattern.test(content)) {
    setErrors(ERROR_TYPE.picturesAndTables, PICTURES_TABLES_ERROR_KEYS.rule3, `Рис${content}`)
  }
}
