import { PdfDataType } from '../../../types'
import { getPageView } from '../../../utils/pdfFormaters/getPageView'

export const getFrameSizeConst = async (
  pdfData: PdfDataType,
  mainFramePageNum: number,
  pdfWidth: number,
  pdfHeight: number
): Promise<{
  kW: number
  kH: number
}> => {
  const [pxPdfWidth, pxPdfHeight] = await getPageView(pdfData, mainFramePageNum)

  const kW = pxPdfWidth / pdfWidth
  const kH = pxPdfHeight / pdfHeight

  return { kW, kH }
}
