import { SetErrors } from '../../../..'
import { ADD_ERROR_KEYS, ERROR_TYPE } from '../../../config/enums'
import { isUppercaseUkrainianWord } from '../../../utils/isUppercaseUkrainianWord'

export const checkAddItem = (item: string, additionItemTitle: string, setErrors: SetErrors): void => {
  const [title, letter, ...rest] = item.split(' ')

  if (rest || title !== additionItemTitle || !letter || letter.length !== 1 || !isUppercaseUkrainianWord(letter)) {
    setErrors(ERROR_TYPE.addition, ADD_ERROR_KEYS.rule2, item)
  }
}
