import { SetErrors } from '../../../..'
import { ERROR_TYPE, PICTURES_TABLES_ERROR_KEYS } from '../../../config/enums'

export const checkTableLinkFormat = (link: string, setErrors: SetErrors) => {
  const pattern = /\(див\.\s+табл\.\s\d+\.\d+\)|\(табл\.\s\d+\.\d+\)/
  const match = link.match(pattern)
  if (!match) {
    setErrors(ERROR_TYPE.picturesAndTables, PICTURES_TABLES_ERROR_KEYS.rule5, link)
  }
}
