import { SetErrors } from '../../../..'
import { CONTENT_LIST } from '../../../config/constants/content'
import { ERROR_TYPE, PAGES_FIDELITY_ERROR_KEYS } from '../../../config/enums'
import { getIsAbstract } from './getIsAbstract'

export const validateTitle = (
  listPageNum: number,
  page: number,
  title: string,
  pageString: string,
  setErrors: SetErrors,
  constants: typeof CONTENT_LIST
) => {
  const isTitleWithoutRegister = pageString.toLowerCase().includes(title.toLowerCase())

  if (page <= listPageNum && !getIsAbstract(title, constants)) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule13, title)
  }

  if (!isTitleWithoutRegister) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule11, title, page.toString())
    return
  }

  const isTitleWithRegister = pageString.includes(title)

  if (!isTitleWithRegister) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule12, title, page.toString())
  }
}
