import { SetErrors } from '../../../..'
import { ABBR_ERROR_KEYS, ERROR_TYPE } from '../../../config/enums'

export const checkAbbreviationListStructure = (referenceList: string[], setErrors: SetErrors): string[] => {
  return referenceList.map((item) => {
    const words = item.split(' ')
    if (words[1] && words[1] !== '–') {
      setErrors(ERROR_TYPE.abbreviation, ABBR_ERROR_KEYS.rule3, item)
    }
    return words[0]
  })
}
