export const isSorted = (array: string[]): { error?: [string, string] } => {
  const sorted = Array.from(array).sort((a, b) => a.localeCompare(b, 'en'))
  for (let i = 0; i < array.length; i++) {
    if (array[i] !== sorted[i]) {
      return { error: [array[i], sorted[i]] }
    }
  }
  return {}
}
