import { includesChapterTitle } from './includesChapterTitle'
import { isChapterConclusion } from './isChapterConclusion'
import { isChapterTitle } from './isChapterTitle'

export type ChapterTitleType = {
  chapter: string
  conclusion?: string
  chapterPosition: number
  conclusionPosition?: number
}

/**
 *
 * @param contentList - array of strings
 * @param chapterName - 'розділ'
 * @param conclusionName - 'висновки'
 * @returns
 */
export const getChaptersData = (
  contentList: string[],
  chapterName: string,
  conclusionName: string
): ChapterTitleType[] => {
  const arr: ChapterTitleType[] = []
  const chapters: ChapterTitleType[] = []

  for (let i = 1; i < contentList.length; i++) {
    const item = contentList[i]
    const includesChapter = includesChapterTitle(item, chapterName)
    if (includesChapter) {
      arr.push({ chapter: item, chapterPosition: i })
    }
  }

  arr.forEach((item) => {
    const isConclusion = isChapterConclusion(item.chapter, conclusionName)
    const isChapter = isChapterTitle(item.chapter, chapterName)
    if (isChapter) {
      chapters.push(item)
    } else if (isConclusion) {
      chapters[chapters.length - 1].conclusion = item.chapter
      chapters[chapters.length - 1].conclusionPosition = item.chapterPosition
    }
  })

  return chapters
}
