import { ContentListConfigType } from '../..'

// дає масив чаптерів з номерами сторінок [start, end]
export const getChaptersPages = async (
  contentListConfig: ContentListConfigType,
  numPages: number
): Promise<[number, number][]> => {
  const chaptersData: [number, number][] = contentListConfig
    .filter(({ title }) => {
      const isChapter = title.match(/розділ /i)
      return isChapter
    })
    .map(({ page }, i, arr) => {
      const start = page
      let end = 0
      const nextChapter = arr[i + 1]?.page
      if (nextChapter) {
        end = nextChapter - 1
      } else {
        const page = contentListConfig.find(({ page, title }) => page > start && title.match(/висновки/i))?.page
        end = page ? page - 1 : numPages
      }

      return [start, end]
    })

  return chaptersData
}
