import { ContentListConfigType } from '..'
import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../config/constants/content'
import { FrameHeightsData, PdfDataType } from '../../types'
import { checkAddItems } from './src/checkAddItems'
import { getAddArr } from './src/getAddArr'

const ADDITION_ITEM_TITLE = 'Додаток'

export const checkAddService = async (
  pdfData: PdfDataType,
  setErrors: SetErrors,
  {
    contentListConfig,
    frameHeightsData,
  }: {
    contentListConfig: ContentListConfigType
    frameHeightsData: FrameHeightsData
  }
): Promise<void> => {
  const addArr = await getAddArr(
    pdfData,
    contentListConfig,
    CONTENT_LIST.addition,
    ADDITION_ITEM_TITLE,
    frameHeightsData
  )

  if (!addArr) return

  checkAddItems(addArr, ADDITION_ITEM_TITLE, setErrors)
}
