export const getTransformedReference = (references: string): number[] => {
  const numbers = references
    .split(',')
    .map((number) => {
      if (number.includes('-')) {
        const [start, end] = number.split('-')
        const newArr = Array.from({ length: +end - +start + 1 }, (_, index) => +start + index)
        return newArr
      } else {
        return +number
      }
    })
    .flat()
  // todo: add test for this
  const notRepeatedNumbers = numbers.filter((number, index) => numbers.indexOf(number) === index)

  return notRepeatedNumbers
}
