import { SetErrors } from '../../../..'
import { ERROR_TYPE, REF_ORDER_ERROR_KEYS } from '../../../config/enums'

/**
 * ця функція має перевіряти останній символ в строці і виводить помилку в хенлер.
 * Після квадратної дужки може бути пробіл, крапка, кома або нова строка',
 *
 * @param str string of reference matches by pattern
 * @param setErrors handler to set errors
 */
export const checkAfterReferenceSymbols = (str: string, setErrors: SetErrors): void => {
  const lastSymbol = str[str.length - 1]
  if (!lastSymbol.match(/[.,\s\p{P}]/u)) {
    setErrors(ERROR_TYPE.referenceOrder, REF_ORDER_ERROR_KEYS.rule4, str)
  }
}
