import { SetErrors } from '../../../..'
// import { ERROR_TYPE, PICTURES_TABLES_ERROR_KEYS } from '../../../config/enums'
import { checkOrder } from './checkOrder'
import { checkTableLinkFormat } from './checkTableLinkFormat'
import { checkTableTitleFormat } from './checkTableTitleFormat'
import { isLinkFormat } from './isLinkFormat'
import { isUsed } from './isUsed'

export const checkTables = (chapterNum: number, arr: string[], setErrors: SetErrors) => {
  const titleArr: string[] = []
  const linksArr: string[] = []
  arr.forEach((item) => {
    const TITLE = 'Таблиця'
    const isTitle = item.includes(TITLE)

    if (isTitle) {
      const pattern = /Таблиця\s\d+/
      const invalidTitle = !pattern.test(item)
      if (invalidTitle) {
        return
      }

      const titleIndex = item.split(TITLE)[1]
      if (!titleIndex) {
        debugger
        console.error('titleIndex', titleIndex)
      }

      const used = isUsed(chapterNum, titleArr.length + 1, linksArr)
      if (!used) {
        console.log('linksArr', linksArr)

        debugger
        // setErrors(ERROR_TYPE.picturesAndTables, PICTURES_TABLES_ERROR_KEYS.rule1, item)
      }

      checkTableTitleFormat(chapterNum, titleArr.length + 1, titleIndex, setErrors)
      titleArr.push(`${chapterNum}.${titleArr.length + 1}`)
    } else {
      const isContinuationTitle = item.includes('Продовження')
      const isLink = isLinkFormat(item, 'табл')

      if (isLink && !isContinuationTitle) {
        linksArr.push(item)
        checkTableLinkFormat(item, setErrors)
      }
    }
  })

  checkOrder(chapterNum, titleArr, setErrors)
}
