import { TextContent } from 'pdfjs-dist/types/src/display/api'
import { FrameHeightsData, PdfDataType } from '../../types'
import { removePageNumberRow } from './helpers/removePageNumberRow'

type Row = { text: string; y: number }

// todo: add test for frameHeightsData and for hasEOL
export async function getPageStringRows(
  doc: PdfDataType,
  pageNumber: number,
  frameHeightsData?: FrameHeightsData
): Promise<string[]> {
  const page = await doc.getPage(pageNumber)
  const content: TextContent = await page.getTextContent()
  const rows: Row[] = []
  let currentRow = ''
  let lastY = null
  const height = frameHeightsData ? frameHeightsData[pageNumber] : undefined
  for (const item of content.items) {
    if ('str' in item) {
      if ((height && item.transform[5] <= height) || item.hasEOL) {
        continue
      }
      if (lastY === null || item.transform[5] === lastY) {
        currentRow += item.str
      } else {
        rows.push({ text: currentRow, y: lastY })
        currentRow = item.str
      }
      lastY = item.transform[5]
    }
  }
  rows.push({ text: currentRow, y: lastY })
  rows.sort((a, b) => b.y - a.y)

  return removePageNumberRow(rows.map((row) => row.text))
}
