import { TextItem, TextMarkedContent } from 'pdfjs-dist/types/src/display/api'

export const getFilteredPdfPageObjByHeight = (
  items: (TextItem | TextMarkedContent)[],
  isUp: boolean,
  height?: number
): TextItem[] => {
  if (typeof height !== 'number') {
    return items as TextItem[]
  }
  const res = items.filter((item) => {
    if ('str' in item) {
      if (item.hasEOL) return false
      return isUp ? item.transform[5] >= height : item.transform[5] <= height
    }
    return false
  })
  return res as TextItem[]
}
