import { SetErrors } from '../../../..'
import { ERROR_TYPE, REF_ORDER_ERROR_KEYS } from '../../../config/enums'

export const checkIsUsed = (limit: number, numbers: number[], setErrors: SetErrors): void => {
  const allNumbers = Array.from(Array(limit + 1).keys()).slice(1)
  const missingNumbers = allNumbers.filter((number) => numbers.indexOf(number) === -1)

  if (missingNumbers.length > 0) {
    setErrors(ERROR_TYPE.referenceOrder, REF_ORDER_ERROR_KEYS.rule6, missingNumbers.join(', '))
  }
}
