import { ContentListConfigType } from '..'
import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../config/constants/content'
import { ABBR_ERROR_KEYS, ERROR_TYPE } from '../../config/enums'
import { FrameHeightsData, PdfDataType } from '../../types'
import { isSorted } from '../../utils/isSorted'
import { checkAbbreviationListStructure } from './src/checkAbbreviationListStructure'
import { checkAbbreviationListUsage } from './src/checkAbbreviationListUsage'
import { getAbbreviationList } from './src/getAbbreviationList'
import { getAbbreviationListPagesConfig } from './src/getAbbreviationListPagesConfig'

export const checkAbbreviationList = async (
  pdfData: PdfDataType,
  setErrors: SetErrors,
  {
    contentListConfig,
    frameHeightsData,
  }: {
    contentListConfig: ContentListConfigType
    frameHeightsData: FrameHeightsData
  }
): Promise<void> => {
  const abbreviationListConfig = getAbbreviationListPagesConfig(contentListConfig, CONTENT_LIST, setErrors)

  if (!abbreviationListConfig) {
    setErrors(ERROR_TYPE.abbreviation, ABBR_ERROR_KEYS.rule0, CONTENT_LIST.abbreviations.toUpperCase())
    return
  }

  const abrArr = await getAbbreviationList(
    pdfData,
    abbreviationListConfig,
    CONTENT_LIST.abbreviations,
    frameHeightsData
  )

  const { error } = isSorted(abrArr)
  if (error) {
    setErrors(ERROR_TYPE.abbreviation, ABBR_ERROR_KEYS.rule2, error[0], error[1])
  }

  const abrList = checkAbbreviationListStructure(abrArr, setErrors)

  checkAbbreviationListUsage(
    pdfData,
    abbreviationListConfig.start + abbreviationListConfig.length,
    abrList,
    frameHeightsData,
    setErrors
  )
}
