import { findIndexByMatch } from './findIndexByMatch'

/**
 *
 * @param rows  - array of strings
 * @param str1 - first regex
 * @param str2 - last regex
 * @returns - index of first match
 */
export const findPageMatchByCase = (rows: string[], str1: string, str2: string): number => {
  const index1 = findIndexByMatch(rows, str1)
  if (index1 !== -1) {
    return index1
  }

  const index2 = findIndexByMatch(rows, str2)
  if (index2 !== -1) {
    return index2
  } else {
    return -1
  }
}
