import { SetErrors } from '../../../..'
import { ERROR_TYPE, REF_LIST_KEYS } from '../../../config/enums'

export const checkRefListOrder = (referenceList: string[], setErrors: SetErrors): void => {
  let prevNumber = 0
  for (const item of referenceList) {
    const number = Number(item.split('.')[0])
    if (number < prevNumber) {
      setErrors(ERROR_TYPE.referenceList, REF_LIST_KEYS.rule2, `${prevNumber}, ${number}`)
    }
    prevNumber = number
  }
}
