import { TextItem } from 'pdfjs-dist/types/src/display/api'
import { DEFAULT_FRAME_DATA, DEFAULT_FRAME_DATA_COORDS, FrameType } from '../../../config/constants/frame' // update as arguments

/**
 *
 * повертає значення корекції для полів рамки в мм
 */
export const getCorrection = (
  data: TextItem[],
  type: keyof typeof FrameType,
  kW: number,
  kH: number,
  defaultFrameCoords: typeof DEFAULT_FRAME_DATA_COORDS,
  defaultFrameData: typeof DEFAULT_FRAME_DATA
): {
  left: number
  bottom: number
} => {
  const leftArr: number[] = []
  const bottomArr: number[] = []

  const obj = defaultFrameCoords[FrameType[type]]

  Object.keys(obj).forEach((key) => {
    const findCorrectionByDate = data.find((item) => item.str === defaultFrameData[key as keyof typeof obj])

    if (findCorrectionByDate) {
      const l = obj[key as keyof typeof obj].left
      const left = findCorrectionByDate.transform[4] / kW - l
      leftArr.push(left)
      const b = obj[key as keyof typeof obj].bottom
      const bottom = findCorrectionByDate.transform[5] / kH - b
      bottomArr.push(bottom)
    }
  })

  const positions = {
    left: leftArr.length ? leftArr.reduce((a, b) => a + b, 0) / leftArr.length : 0,
    bottom: bottomArr.length ? bottomArr.reduce((a, b) => a + b, 0) / bottomArr.length : 0,
  }

  return positions
}
