import type { TextItem } from 'pdfjs-dist/types/src/display/api.js'
import { CoordsType } from '../../../config/constants/frame'

type GroupNameValidity = {
  data: TextItem[]
  coordsFieldData: CoordsType
  kW: number
  kH: number
  indent: { left: number; bottom: number }
  correction: { left: number; bottom: number }
}

export const getGroupName = ({ data, coordsFieldData, kW, kH, indent, correction }: GroupNameValidity): string => {
  const { left, bottom } = coordsFieldData
  const { left: indentLeft, bottom: indentBottom } = indent
  const { left: correctionLeft, bottom: correctionBottom } = correction
  const leftMin = left[0] + indentLeft + correctionLeft
  const leftMax = left[1] + indentLeft + correctionLeft
  const bottomMin = bottom[0] + indentBottom + correctionBottom
  const bottomMax = bottom[1] + indentBottom + correctionBottom

  const matchedData = data
    .filter((value) => {
      const { transform, height, width } = value
      const hCenter = (transform[4] + width / 2) / kW
      const vCenter = (transform[5] + height / 2) / kH
      const check = hCenter > leftMin && hCenter < leftMax && vCenter > bottomMin && vCenter < bottomMax

      return check
    })
    .sort((a, b) => {
      return b.transform[5] - a.transform[5]
    })

  const groupedStrings = []
  let currentGroup: TextItem[] = []
  let currentTransform5: number | null = null

  for (const item of matchedData) {
    if (currentTransform5 === null) {
      currentTransform5 = item.transform[5]
    }

    if (item.transform[5] === currentTransform5) {
      currentGroup.push(item)
    } else {
      currentGroup.sort((a, b) => a.transform[4] - b.transform[4])

      const combinedString = currentGroup.map((obj) => obj.str).join('')
      groupedStrings.push(combinedString)

      currentGroup = [item]
      currentTransform5 = item.transform[5]
    }
  }

  if (currentGroup.length > 0) {
    currentGroup.sort((a, b) => a.transform[4] - b.transform[4])
    const combinedString = currentGroup.map((obj) => obj.str).join('')
    groupedStrings.push(combinedString)
  }

  return groupedStrings.join('')
}
