import { ContentListConfigType } from '../..'
import { SetErrors } from '../../../..'
import { CONTENT_LIST } from '../../../config/constants/content'
import { ABBR_ERROR_KEYS, ERROR_TYPE } from '../../../config/enums'

export const getAbbreviationListPagesConfig = (
  list: ContentListConfigType,
  config: typeof CONTENT_LIST,
  setErrors: SetErrors
): { start: number; length: number } | null => {
  const abbreviationsTitle = config.abbreviations
  const abb = list.find((item) => item.title.toLowerCase() === abbreviationsTitle.toLocaleLowerCase())
  if (!abb) return null

  const nextChapter = list.filter((item) => item.page > abb?.page).sort((a, b) => a.page - b.page)[0]
  const NextChapterName = nextChapter?.title.toLowerCase()

  if (NextChapterName.toLocaleLowerCase() !== config.introduction.toLowerCase()) {
    setErrors(ERROR_TYPE.abbreviation, ABBR_ERROR_KEYS.rule1, abbreviationsTitle.toUpperCase())
  }
  const length = nextChapter.page - abb.page
  return { start: abb.page, length }
}
