import { SetErrors } from '../../../..'
import { ABBR_ERROR_KEYS, ERROR_TYPE } from '../../../config/enums'
import { FrameHeightsData, PdfDataType } from '../../../types'
import { getPageStringRows } from '../../../utils/pdfFormaters/getPageStringRows'

export const checkAbbreviationListUsage = async (
  pdfData: PdfDataType,
  pageNum: number,
  brtList: string[],
  frameHeightsData: FrameHeightsData,
  setErrors: SetErrors
): Promise<void> => {
  const pagesLength = pdfData.numPages
  for (const abr of brtList) {
    let used = false
    for (let i = pageNum; i <= pagesLength; i++) {
      const res = await getPageStringRows(pdfData, i, frameHeightsData)
      if (!res.includes(abr)) {
        used = true
      } else {
        break
      }
    }
    if (!used) {
      setErrors(ERROR_TYPE.abbreviation, ABBR_ERROR_KEYS.rule4, abr)
    }
  }
}
