import { SetErrors } from '../../../..'
import { ERROR_TYPE, REF_ORDER_ERROR_KEYS } from '../../../config/enums'

export const checkOrder = (list: string, numbers: number[], setErrors: SetErrors): void => {
  const stack = []
  for (let i = 0; i < numbers.length; i++) {
    if (stack.length > 0 && numbers[i] < stack[stack.length - 1]) {
      setErrors(ERROR_TYPE.referenceOrder, REF_ORDER_ERROR_KEYS.rule5, list)
      return
    }
    stack.push(numbers[i])
  }
}
