import { getIsValidRefNumber } from './getIsValidRefNumber'

/**
 *
 * @param rows parsed pdf page rows
 * @returns
 */
export const getListFromStringRows = (rows: string[]): string[] => {
  const referenceList: string[] = []
  let currentReference = ''

  for (let line of rows) {
    if (line) {
      const numberMatch = line.match(/^\d+\./)
      if (numberMatch && getIsValidRefNumber(numberMatch[0])) {
        if (currentReference) {
          referenceList.push(currentReference)
        }
        currentReference = line
      } else {
        currentReference += ' ' + line
      }
    }
  }

  if (currentReference) {
    referenceList.push(currentReference)
  }
  return referenceList
}
