/**
 * is not case sensitive function to find index of first match in array of strings
 *
 * @param rows - array of strings
 * @param str - string to match
 * @returns index of first match or -1 if not found
 */
export const findIndexByMatch = (rows: string[], str: string): number => {
  const regex = new RegExp(str, 'i')
  return rows.findIndex((row) => row.match(regex))
}
