import { SetErrors } from '../../../..'
import { VAK_PATTERNS } from '../../../config/constants/reference'
import { ERROR_TYPE, REF_LIST_KEYS } from '../../../config/enums'

export function checkReferenceElRes(setErrors: SetErrors, referenceItem: string, patterns: typeof VAK_PATTERNS): void {
  const { elResLink, elResTitle } = patterns
  // Перевірка, чи є інформація про тип джерела
  if (!referenceItem.includes(elResTitle)) {
    setErrors(ERROR_TYPE.referenceList, REF_LIST_KEYS.rule4, referenceItem)
  }

  // Перевірка, чи є слова "Режим доступу до ресурсу:"
  if (!referenceItem.includes(elResLink)) {
    setErrors(ERROR_TYPE.referenceList, REF_LIST_KEYS.rule5, referenceItem)
  }
}
