import { ContentListConfigType } from '..'
import { concatTitle } from './concatTitle'
import { definePageNum } from './definePageNum'
import { getIsNextSubItem } from './getIsNextSubItem'

/**
 *
 * @param contents - array of strings, parsed from pdf
 * @returns
 */
export const getContentData = (contents: string[]): ContentListConfigType => {
  const results = []
  let item = ''
  let savedChapter = ''

  for (let i = 0; i < contents.length; i++) {
    const content = contents[i]
    const pageNum = definePageNum(content)

    if (typeof pageNum === 'number') {
      if (savedChapter) {
        results.push({
          title: savedChapter,
          page: pageNum,
        })
        savedChapter = ''
      }
      const pageData = {
        title: concatTitle(item, content),
        page: pageNum,
      }
      results.push(pageData)
      item = ''
    } else if (getIsNextSubItem(contents[i + 1] ?? '')) {
      // todo: add error - Не вказана сторінка розділу для: {title}
      savedChapter += `${content} `
    } else {
      item += `${content} `
    }
  }
  // console.log('results', results)
  return results
}
