import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../config/constants/content'
import { FrameHeightsData, PdfDataType } from '../../types'
import { getContentListPageNumber } from '../../utils/getContentListPageNumber'
import { getPageString } from '../../utils/pdfFormaters/getPageString'
import { ContentListConfigType } from '../checkPagesFidelity'
import { checkAfterReferenceSymbols } from './src/checkAfterReferenceSymbols'
import { checkInsideReferenceSymbols } from './src/checkInsideReferenceSymbols'
import { checkReferenceFidelity } from './src/checkReferenceFidelity'
import { checkStarrSymbols } from './src/checkStarrSymbols'
import { findReferenceMatches } from './src/findReferenceMatches'

export const checkReferenceOrder = async (
  pdfData: PdfDataType,
  setErrors: SetErrors,
  {
    contentListConfig,
    length,
    frameHeightsData,
  }: { contentListConfig: ContentListConfigType; length: number; frameHeightsData: FrameHeightsData }
): Promise<void> => {
  if (!contentListConfig.length || !length) {
    console.log('Impossible to check reference order, contentListConfig is empty')
    return
  }

  const start = contentListConfig[0].page
  const end = getContentListPageNumber(contentListConfig, CONTENT_LIST.litList) as number

  const fullText: string[] = []
  for (let i = start; i < end; i++) {
    const text = await getPageString(pdfData, i, frameHeightsData)
    fullText.push(text)
  }
  const arr = findReferenceMatches(fullText.join(' '))
  arr.forEach((item) => {
    checkStarrSymbols(item, setErrors)
    checkInsideReferenceSymbols(item, setErrors)
    checkAfterReferenceSymbols(item, setErrors)
  })
  checkReferenceFidelity(arr, setErrors, length)
}
