import { CONTENT_LIST } from '../../../config/constants/content'
import { FrameHeightsData, PdfDataType } from '../../../types'
import { getPageStringRows } from '../../../utils/pdfFormaters/getPageStringRows'
import { findPageMatchByCase } from './findPageMatchByCase'

/**
 *
 * @param pdfData - pdf data
 * @returns Promise<{ listData: string[]; listPageNum: number }> - list data and list page number
 */
export const getContentList = async (
  pdfData: PdfDataType,
  constants: typeof CONTENT_LIST,
  frameHeightsData: FrameHeightsData
): Promise<{ listData: string[]; listPageNum: number }> => {
  const startReg = new RegExp(constants.contentTitle, 'i')

  const pagesNum = pdfData.numPages
  let listData: string[] = []
  let listPageNum = 0

  for (let i = 1; i <= pagesNum; i++) {
    // todo: need to refactor in case with frame(we don't need data in frame), need to use data by height params to cut frame data
    const rows = await getPageStringRows(pdfData, i, frameHeightsData)
    const isListPage = rows[0].match(startReg)
    if (isListPage) {
      listPageNum = i
      const listEndPageIndex = findPageMatchByCase(rows, constants.addition, constants.litList)
      if (listEndPageIndex !== -1) {
        listData = rows.slice(1, listEndPageIndex + 1)
        break
      } else {
        for (let j = i + 1; j <= pagesNum; j++) {
          const rows2 = await getPageStringRows(pdfData, j, frameHeightsData)

          const listEndPageIndex = findPageMatchByCase(rows2, constants.addition, constants.litList)
          if (listEndPageIndex !== -1) {
            listData = [...rows.slice(1), ...rows2.slice(0, listEndPageIndex + 1)]
            break
          }
        }
        break
      }
    }
  }
  return { listData, listPageNum }
}
