import { FrameHeightsData, PdfDataType } from '../../types'
import { getPageStringRows } from './getPageStringRows'

export async function getPageString(
  doc: PdfDataType,
  pageNumber: number,
  frameHeightsData?: FrameHeightsData
): Promise<string> {
  const arr = await getPageStringRows(doc, pageNumber, frameHeightsData)
  return arr.join(' ')
}
