import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../config/constants/content'
import { ERROR_TYPE, PAGES_FIDELITY_ERROR_KEYS } from '../../config/enums'
import { ContentListCheckConfig, FrameHeightsData, PdfDataType } from '../../types'
import { getPageStringRows } from '../../utils/pdfFormaters/getPageStringRows'
import { checkList } from './src/checkList'
import { getContentData } from './src/getContentData'
import { getContentList } from './src/getContentList'
import { validateTitle } from './src/validateTitle'

export type ContentListConfigType = {
  title: string
  page: number
}[]

export const checkPagesFidelity = async (
  pdfData: PdfDataType,
  setErrors: SetErrors,
  { isChapterConclusionRequired, chapterSize }: ContentListCheckConfig,
  frameHeightsData: FrameHeightsData
): Promise<ContentListConfigType> => {
  const { listData, listPageNum } = await getContentList(pdfData, CONTENT_LIST, frameHeightsData)

  // todo: is it possible to be here?
  if (!listData.length) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_ERROR_KEYS.rule1)
    return []
  }

  const list = getContentData(listData)

  checkList(
    setErrors,
    list.map((item) => item.title),
    CONTENT_LIST,
    isChapterConclusionRequired,
    chapterSize
  )

  for (const item of list) {
    const { title, page } = item

    const pageData = await getPageStringRows(pdfData, page, frameHeightsData)
    const pageString = pageData.join(' ')
    validateTitle(listPageNum, page, title, pageString, setErrors, CONTENT_LIST)
  }

  return list
}
